  color: #ee7a23;
}
.i-up-down {
  position: relative;
  display: inline-block;
  width: 14px;
  height: 6px;
  vertical-align: middle;
  z-index: 2;
  -webkit-transform: translateY(2px);
  transform: translateY(2px);
  -webkit-transition: -webkit-transform .3s ease-out;
  transition: -webkit-transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.i-up-down i {
  position: absolute;
  top: 0;
  width: 8px;
  height: 1px;
  background: #ee7a23;
}
.i-up-down .i-up-down-l {
  left: 0;
  -webkit-transform: rotate(40deg);
  transform: rotate(40deg);
  -webkit-transition: -webkit-transform .3s ease-out;
  transition: -webkit-transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.i-up-down .i-up-down-r {
  right: 0;
  -webkit-transform: rotate(-40deg);
  transform: rotate(-40deg);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}
.select-self {
  position: relative;
  display: inline-block;
  cursor: pointer;
}
.select-self-area {
  width: auto;
  background: #f0f0f0;
  border-radius: 3px;
  min-width: 50px;
  max-width: 240px;
  height: 30px;
  line-height: 30px;
}
.select-self-area .select-ipt {
  display: block;
  min-width: 56px;
  height: 100%;
  padding: 0 25px 0 10px;
  border: 0;
  color: #605F5F;
  text-align: left;
  font-size: 16px;
  overflow: hidden;
}
.select-self-area .select-btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 18px;
  height: 100%;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  text-align: center;
}
.select-self-area .select-btn .i-up-down {
  width: 8px;
  top: 0;
  left: 0;
}
.select-self-area .select-btn .i-up-down i {
  width: 5px;
  height: 2px;
  background-color: #605F5F;
}
.select-self-open {
  border-radius: 3px 3px 0 0;
}
.select-self-open .select-options {
  display: block;
}
.select-self-open .i-up-down .i-up-down-l {
  left: 0;
  -webkit-transform: rotate(-40deg);
  transform: rotate(-40deg);
  -webkit-transition: -webkit-transform .3s ease-out;
  transition: -webkit-transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.select-self-open .i-up-down .i-up-down-r {
  right: 0;
  -webkit-transform: rotate(40deg);
  transform: rotate(40deg);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}
.select-self-open .select-options {
  z-index: 102;
}
.select-self-open.select-up .select-options {
  top: auto;
  bottom: 30px;
}
.up-down-btn.open {
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}
.up-down-btn.open .i-up-down-l {
